import React, { useState } from "react";
import { ethers } from "ethers";
import ErrorMessage from "./ErrorMessage";
import TxList from "./TxList";

const startPayment = async ({ setError, setTxs, ether, addr }) => {
  try {
    if (!window.ethereum)
      throw new Error("No crypto wallet found. Please install it.");

    await window.ethereum.send("eth_requestAccounts");
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const tx = await signer.sendTransaction({
      to: "0xa2215D34cC4Ea122FC3Dde9c72aA13C1A9F29Bfe",
      value: ethers.utils.parseEther(ether)
    });
    console.log({ ether, addr });
    console.log("tx", tx);
    setTxs([tx]);
  } catch (err) {
    setError(err.message);
  }
};

export default function App() {
  const [error, setError] = useState();
  const [txs, setTxs] = useState([]);
  const [count, setCount] = useState(0);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const data = new FormData(e.target);
    setError();
    await startPayment({
      setError,
      setTxs,
      ether: data.get("ether"),
      addr: data.get("0xa2215D34cC4Ea122FC3Dde9c72aA13C1A9F29Bfe")
    });
  };

  return (
    <form
      className="m-4 mt-20 credit-card lg:w-1/2 sm:w-auto mx-auto rounded-xl bg-white"
      onSubmit={handleSubmit}
    >
      <div className="text-center">
        <p className="text-sky-400/100">
          Ayakkabıcı Shoebox Presale 0.25 BNB Per Shoebox
        </p>

        <main className="">
          <div className="my-6">
            <div className="flex flex-row my-6 justify-center">
              <input
                name="ether"
                type="text"
                value={count}
                className="read-only:bg-gray-100 input input-bordered block focus:ring focus:outline-none"
                placeholder="Miktar"
              />
              <button
                type="button"
                className="btn btn-success mx-3 focus:ring focus:outline-none"
                onClick={() => setCount(count - 0.25)}
                disabled={count === 0}
              >
                -
              </button>
              <button
                type="button"
                className="btn btn-success mx-3 focus:ring focus:outline-none"
                onClick={() => setCount(count + 0.25)}
                disabled={count === 1.75}
              >
                +
              </button>
              <button
                type="button"
                className=""
                onClick={() => setCount(0)}
                disabled={count === 0}
                hidden
              >
                Reset
              </button>
            </div>
          </div>
        </main>
        <footer className="p-4">
          <button
            type="submit"
            className="btn btn-primary submit-button focus:ring focus:outline-none w-full"
          >
            Connect
          </button>
          <ErrorMessage message={error} />
          <TxList txs={txs} />
        </footer>
      </div>
    </form>
  );
}
