import React from "react";

const Headers = () => {
  return (
    <div className="shadow-md w-full fixed top-0">
      <div className="md:flex bg-white flex justify-between py-4 px-7">
        <div className="font-bold text-3x1 text-gray-800 cursor-pointer">
          YIGITATAKAN
        </div>
        <div className="flex md:px10">
          <div className="text-gray-800 cursor-pointer md:px-2">
            <ion-icon name="logo-twitter"></ion-icon>
          </div>
          <div className="text-gray-800 cursor-pointer md:px-2">
            <ion-icon name="logo-instagram"></ion-icon>
          </div>
          <div className="text-gray-800 cursor-pointer md:px-2">
            <ion-icon name="logo-github"></ion-icon>
          </div>
          <div className="text-gray-800 cursor-pointer md:px-2">
            <ion-icon name="logo-youtube"></ion-icon>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Headers;
